.profile__description {
    font-size: 18px;
    font-weight: normal;
    line-height: 22px;
    margin: 0;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 950px) {
    .profile__description {
        text-align: center;
        font-size: 14px;
        line-height: 17px;
    }
}
