.profile__avatar {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  margin-right: 30px;
  cursor: pointer;
  background-size: cover;
  position: relative;
  background-image: url("../../../images/avatar.jpg"); /* default image */
  background-repeat: no-repeat;
  background-position: center;
}

@media screen and (max-width: 950px) {
  .profile__avatar {
    margin: 0px 0px 30px 0px;
  }
}
